               for(let i=0;i<arr.length;i++){
                  let item = arr[i]
                    if(item.codeValue=="professional"||
                          item.codeValue=="0007"||
                          item.codeValue=="0008"||item.codeValue=="0006"){
                         continue
                    }
                     _this.expertMajorDict.push(item)
