      let leaderMajor = {}
      leaderMajor.type = 1
      leaderMajor.professional = _this.autoSelectExpertParam.leaderMajor

      //校验
      if( _this.autoSelectExpertParam.leaderMajor==""){
          _this.$message({
                showClose: true,
                message: '请选择组长专业',
                type: 'warning'
              });
           return false
      }
      leaderMajor.professionalStr = _this.getProfessionalStr(_this.autoSelectExpertParam.leaderMajor)
      leaderMajor.itemId = _this.tempItemId
      leaderMajor.feaAuditId = _this.tempFeaAuditid
      param.push(leaderMajor)

      //技经专家
      let goodExpert = {}
      goodExpert.type = 3
      goodExpert.count = _this.autoSelectExpertParam.goodExpertNum
      // if(_this.autoSelectExpertParam.goodExpertNum<1){
      //       _this.$message({
      //           showClose: true,
      //           message: '技经专家人数要大于1',
      //           type: 'warning'
      //         });
      //      return false
      // }
      goodExpert.itemId = _this.tempItemId
      goodExpert.feaAuditId = _this.tempFeaAuditid

      param.push(goodExpert)

      for(let i=0;i<_this.autoSelectExpertParam.expertMajor.length;i++){
          let temp = _this.autoSelectExpertParam.expertMajor[i]
          let tempParam = {}
          tempParam.type = 2
          tempParam.professional = temp.major
          if(tempParam.professional==''){
            _this.$message({
                    showClose: true,
                    message: '请选择专家专业',
                    type: 'warning'
                  });
              return false
          }
          tempParam.professionalStr = _this.getProfessionalStr(temp.major)
          tempParam.count = temp.num
          if(tempParam.count<1){
            _this.$message({
                    showClose: true,
                    message: '专家人数要大于0',
                    type: 'warning'
                  });
              return false
          }
          tempParam.itemId = _this.tempItemId
          tempParam.feaAuditId = _this.tempFeaAuditid
          param.push(tempParam)
      }
      _this.autoLoading = true
      autoSelectExperts(param).then(response=>{
        _this.autoLoading = false
          if(response.data.successful){
              // _this.$message({
              //       showClose: true,
              //       message: '选择专家成功',
              //       type: 'success'
              //     });
              // _this.getTable(1,10)
              // _this.dialogFormVisible = false
              _this.showExpectDialog = false

              //选择完专家之后查询已选则的转件
              _this.showSelectExpertResult = true
              let labelArr = response.data.resultValue.tableLable.items
 
              _this.selectedExpertLable = _this.getLabelList(labelArr)
              _this.selectedExpertDictMap = _this.getDictArr(labelArr)
              _this.selectedExpertColumnMap = _this.getColumnTypeArr(labelArr)

              let tableData = response.data.resultValue.tableData
              _this.totalArr = []
              for(let i=0;i<tableData.length;i++){
                  let arrT = tableData[i]
                  for(let j=0;j<arrT.expertsList.length;j++){
                       _this.totalArr =   _this.totalArr.concat(arrT.expertsList[j])
                  }  
              }
              let leaderArr = []
              let groupExpertArr = []
              let goodExpertArr = []
              for(let i=0;i< _this.totalArr.length;i++){
                    let tempObj =  _this.totalArr[i]
                    if(tempObj.isLeader==1){//组长
                        leaderArr.push(tempObj)
                    }else if(tempObj.isLeader==3){
                         goodExpertArr.push(tempObj)
                    }else if(tempObj.isLeader==2){
                       
                        groupExpertArr.push(tempObj)
                    }
                }

             _this.selectedExpertLeader = _this.getShowDateTable(leaderArr,_this.selectedExpertDictMap,_this.selectedExpertColumnMap)
             _this.selectedExpert = _this.getShowDateTable(groupExpertArr,_this.selectedExpertDictMap,_this.selectedExpertColumnMap)
             _this.selectedGootExpert = _this.getShowDateTable(goodExpertArr,_this.selectedExpertDictMap,_this.selectedExpertColumnMap)


          }else{
               let str = response.data.resultHint
               _this.$message({
                    showClose: true,
                    message: '选择专家失败 '+str,
                    dangerouslyUseHTMLString: true,
                    type: 'error'
                });
          }
      })
    },
