      console.log(`当前页: ${val}`);
      this.currentPage = val
      this.getTable(val,this.currentNum)
    },
    getTable(page,size){
      let _this = this
      _this.loading = true
      let paramSelect = {}
      paramSelect.index = page
      paramSelect.pageSize = size
      if(_this.param.isPassed==0){
          // paramSelect.itemPhaseDet = '5,11,14,16'
          paramSelect.itemPhaseDet = '9,15,18,20'
      }else{
          paramSelect.itemPhaseDet = _this.param.itemPhaseDet
      }

      paramSelect.itemName = _this.param.itemName
      paramSelect.createOrg = _this.param.deptCode
      paramSelect.itemLevel = _this.param.itemLevel
      paramSelect.speciality = _this.param.speciality


      // param:{
      //   "pageIndex": 1,
      //   "pageSize": 10,
      //   "isPassed":0,
      //   "deptCode":"",
      //   "itemType": "",
      //   "speciality": "",
      //   "itemPhaseDet":""
      // },

       prjStFeaAuditNew(paramSelect).then(response => {//{}写需要的参数
          console.log('response',response)//后端返回数据
          if(response.data.successful){
              _this.totalNum = response.data.resultValue.totalNum
              _this.tableLabel = response.data.resultValue.tableLable
              let arr = response.data.resultValue.tableLable
              _this.tableLabel = _this.getLabelList(arr)
              _this.dictMap = _this.getDictArr(arr)
              _this.columnTypeMap = _this.getColumnTypeArr(arr)
              _this.tableData = response.data.resultValue.tableData
              _this.tableShowData = _this.getShowDateTable(_this.tableData,_this.dictMap,_this.columnTypeMap)

          }
          _this.loading = false
          
        }).catch(error => {
          console.log(error)//error
        })
        
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          // console.log(key + ':' + value);
          if(key=="itemPhaseDet"){
               copyObj["itemPhaseDetT"] = value;
          }
          if(key=='speciality'){
              copyObj["specialityT"] = value;
          }
         
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else if(columnTypeMap.get(key)=="DATE"){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value)
               copyObj[key] = timeStr;
          }else{
            copyObj[key] = value;
          }
        }
        arr.push(copyObj);
      }
      return arr;
    },
    //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
     //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
     //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y+'-'+this.add0(m)+'-'+this.add0(d);
    },
    add0(m){
      return m<10?'0'+m:m;
    },
  }
}
</script>
